


import center from './collection/img4.png'
import right from './collection/img.png'
import left from './collection/ing5.png'
import {useEffect, useRef, useState } from 'react';

import {TweenMax,Power3,gsap,Expo,Circ,Bounce} from 'gsap'
import { TimelineLite } from 'gsap/gsap-core';
import {useNavigate} from 'react-router-dom';



function App() {

  const navigate = useNavigate();
  const centerImageClick=()=>{
  navigate('/chart');
  }

  let app = useRef(null);
  let leftimg = useRef(null);
  let centerimg = useRef(null);
  let rightimg = useRef(null);

  let tl = new TimelineLite();

  var tll = gsap.timeline();

  useEffect(()=>{
    TweenMax.to(app,0,{css:{visibility:'visible'}})
    console.log('yes');

    

  tll.from('body',{scale:1,duration:3,opacity:0,ease:Circ.easeIn,delay:0.2})
  .from(leftimg,{x:2000,duration:2,ease:Power3.easeOut,delay:0.2})
  .from(centerimg,{x:2000,duration:2,ease:Power3.easeOut,delay:0.2})
  .from(rightimg,{x:1000,duration:2,ease:Power3.easeOut,delay:0.2})
  


  },[])

  const [l,setL]=useState('3');
  const [c,setC]=useState('6');
  const [r,setR]=useState('3');

  return (
    <div className="row g-0 no-gutters h-100 w-100" ref={el=> app=el}>
      <div className="col-12 images " >
      
      <div className=" row g-0 no-gutters"  >

      <img src={left} className={`img col-md-`+l +` col-sm-8`} 
       ref={el=> leftimg=el} 
      onMouseEnter={()=>{
        setL('4');
        setC('6');
        setR('2');
      }}   
      onMouseLeave={()=>{
        setL('3');
        setC('6');
        setR('3');

      }}
      
      />
      <img src={center} className={`img col-md-`+c +` col-sm-8`}
       ref={el=> centerimg=el}
      onMouseEnter={()=>{
        setL('2');
        setC('8');
        setR('2');
      }}   
      onMouseLeave={()=>{
        setL('3');
        setC('6');
        setR('3');

      }} 
      onClick={centerImageClick}
      />
      
      <img src={right} className={`img2 col-md-`+r +` col-sm-8`}
       ref={el=> rightimg=el}
      onMouseEnter={()=>{
        setL('2');
        setC('6');
        setR('4');
      }}   
      onMouseLeave={()=>{
        setL('3');
        setC('6');
        setR('3');

      }}
      />



      </div>

      </div>

      
      
      
    </div>
  );
}

export default App;
